.selectric-wrapper {
  position: relative;
  cursor: pointer;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  line-height: 144.9%;
}

.selectric-responsive {
  width: 100%;
}

.selectric {
  border: 2px solid #D3D360;
  border-radius: 37px;
  background: #FFFFFF;
  position: relative;
  overflow: hidden;
  box-shadow: 0px 29px 26px -20px rgba(211, 211, 96, 0.37);
}
.selectric .label {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0 51px 0 39px;
  font-size: 18px;
  line-height: 51px;
  color: #161616;
  height: 51px;
  user-select: none;
}
.selectric .button {
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 51px;
  height: 51px;
  line-height: 51px;
  background-color: #FFFFFF;
  color: rgb(29, 143, 189);
  text-align: center;
  font: 0/0 a;
  *font: 20px/51px Lucida Sans Unicode, Arial Unicode MS, Arial;
  padding: 0 15px;
}
.selectric .button:after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  border-right: 1px solid #bbb;
  border-bottom: 1px solid #bbb;
  width: 6px;
  height: 6px;
  transform: rotate(45deg);
}

.selectric-focus .selectric {
  border-color: #a1a12c;
}

.selectric-hover .selectric {
  border-color: #c8c838;
}
.selectric-hover .selectric .button {
  color: #166e91;
}
.selectric-hover .selectric .button:after {
  border-top-color: #166e91;
}

.selectric-open {
  z-index: 99;
}
.selectric-open .selectric {
  border-color: #c8c838;
}
.selectric-open .selectric-items {
  display: block;
}

.selectric-disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default;
  user-select: none;
}

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
}
.selectric-hide-select select {
  position: absolute;
  left: -100%;
}
.selectric-hide-select.selectric-is-native {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
}
.selectric-hide-select.selectric-is-native select {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border: none;
  z-index: 1;
  box-sizing: border-box;
  opacity: 0;
}

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important;
}

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important;
}

/* Items box */
.selectric-items {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: #FFFFFF;
  border: 1px solid #c8c838;
  z-index: -1;
  box-shadow: 0 0 10px -6px;
}
.selectric-items .selectric-scroll {
  height: 100%;
  overflow: auto;
}
.selectric-above .selectric-items {
  top: auto;
  bottom: 100%;
}
.selectric-items ul, .selectric-items li {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 18px;
  line-height: 20px;
  min-height: 20px;
}
.selectric-items li {
  display: block;
  padding: 10px;
  color: #666;
  cursor: pointer;
}
@media all and (max-width: 500px) {
  .selectric-items li {
    font-size: 12px;
  }
}
.selectric-items li.selected {
  background: #E0E0E0;
  color: #444;
}
.selectric-items li.highlighted {
  background: #e3e3af;
  color: #444;
}
.selectric-items li:hover {
  background: #e3e3af;
  color: #444;
}
.selectric-items .disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default !important;
  background: none !important;
  color: #666 !important;
  user-select: none;
}
.selectric-items .selectric-group .selectric-group-label {
  font-weight: bold;
  padding-left: 10px;
  cursor: default;
  user-select: none;
  background: none;
  color: #444;
}
.selectric-items .selectric-group.disabled li {
  filter: alpha(opacity=100);
  opacity: 1;
}
.selectric-items .selectric-group li {
  padding-left: 39px;
}

